/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edgtf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .edgtf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .edgtf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .edgtf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }
        
        .edgtf-cart-number {
            position: absolute;
            top: -5px;
            right: -5px;
            display: block;
            width: 12px;
            height: 12px;
            font-size: 8px;
            line-height: 12px;
            text-align: center;
            color: #fff;
            background: $first-main-color;
            border-radius: 100%;
        }

        &.edgtf-header-cart-icon-pack {
            .edgtf-cart-icon {
                font-size: 20px;
                line-height: 1em;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        &.edgtf-header-cart-svg-path {
            .edgtf-cart-icon {

                >* {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .edgtf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            max-height: 1000px;
            visibility: visible;
        }
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.edgtf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 320px;
    max-height: 0;
    overflow: hidden;
    background-color: rgba($default-heading-color, .91);
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include edgtfTransition(max-height .8s ease, visibility .5s ease);

    ul {
        margin: 25px;
        padding: 0;

        li {
            @include edgtfTableLayout();
            padding: 13px 0;
			color: #fff;
            box-sizing: border-box;

            &.edgtf-empty-cart {
                color: #fff;
                text-align: center;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .edgtf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 72px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .edgtf-item-info-holder {
        @include edgtfTableCellLayout();
        vertical-align: top;
        padding-left: 20px;

        .edgtf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
			color: #fff;
            box-sizing: border-box;

			a {
				color: #fff;
			}
        }

        .remove {
            position: absolute;
            top: -3px;
            right: 0;
			color: #949494;
	        font-size: 22px;
	        line-height: 1;
            
            &:hover {
                color: #fff;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .edgtf-quantity {
            display: block;
            margin: 3px 0 0;
			color: #949494;
			font-size: 13px;
			line-height: 2;
        }

        .amount {
            display: block;
            margin: 3px 0 0;
            color: $first-main-color;
			font-family: $additional-text-font;
			font-size: 18px;
			font-weight: 700;
			line-height: 1.4;
        }
    }

    .edgtf-cart-bottom {
        padding: 11px 0 0;

        .edgtf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 24px;
			padding: 14px 0 16px;
			border-top: 1px solid #4a4a4a;
			border-bottom: 1px solid #4a4a4a;

            > * {
                display: table-cell;
                vertical-align: top;
                line-height: 20px;
            }
            
            .edgtf-total {
                font-size: 15px;
                color: #949494;
            }

            .edgtf-total-amount {
				font-family: $additional-text-font;
				color: $first-main-color;
				font-size: 18px;
				font-weight: 700;
				text-align: right;
			}
        }

        .edgtf-view-cart {
            @include edgtfRelativeHolderLayout();
            height: 50px;
            line-height: 50px;
            padding: 0 41px;
            font-size: 12px;
			font-weight: 700;
            color: #000;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include edgtfTransition();

            &:after {
                position: absolute;
                content: '';
                top: -6px;
                left: -6px;
                width: 100%;
                height: 100%;
                padding: 5px;
                border: 1px solid #fff;
                box-sizing: content-box;
                @include edgtfTransition(padding .3s cubic-bezier(.21,.6,.44,2.18),top .3s cubic-bezier(.21,.6,.44,2.18),left .3s cubic-bezier(.21,.6,.44,2.18));
            }

            span {
                display: block;
            }

            &:hover {
                &:after {
                    top: -1px;
                    left: -1px;
                    padding: 0;
                    @include edgtfTransition(padding .3s ease,top .3s ease,left .3s ease);
                }
            }
        }
    }
}

.edgtf-vertical-menu-area {

    .edgtf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */