/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */

.woocommerce-pagination {
    @include edgtfRelativeHolderLayout();
    margin: 31px 0 91px;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;
		text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0;
			font-family: $additional-text-font;
			font-size: 18px;
	        font-weight: 700;

            a,
            span {
                position: relative;
                display: inline-block;
	            width: auto;
                margin: 0 10px;
                padding: 0;
                vertical-align: middle;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                color: $default-heading-color;
                @include edgtfTransition(color .2s ease-out);

                &:hover,
                &.current {
                    color: $first-main-color;
                }
            }

			&.edgtf-pag-next {
				position: absolute;
				right: 0;
			}

			&.edgtf-pag-prev {
				position: absolute;
				left: 0;
			}

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    margin: 0 6px 0 4px;

                    &:before {
                        position: relative;
                        display: inline-block;
						vertical-align: middle;
						font-family: 'FontAwesome';
						font-size: 18px;
						line-height: 27px;
						-webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }
    
                &.prev {
        
                    &:before {
						content: "\f060";
                    }
                }
    
                &.next {
        
                    &:before {
                        content: "\f061";
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */