/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.edgtf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 15px;
        float: left;
		color: #a3a3a3;
    }

    .woocommerce-result-count {
        line-height: 44px;
    }

    .woocommerce-ordering {
        text-align: right;
        
        .orderby {
            width: auto;
	        min-width: 231px;
	        margin: 0;
	        text-align: initial;
        }

		.select2-selection--single {
			border: 0;
		}
	}

	.select2-container--open {

		.select2-dropdown {
			border: 0;
		}
	}
}
/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */