/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {

    .edgtf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        padding: 5px 0;
        border: 1px solid $default-border-color;

        .edgtf-quantity-input,
        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-heading-color;
            background-color: transparent;
            text-align: center;
            line-height: 47px;
            border: none;
            box-sizing: border-box;
        }

        .edgtf-quantity-input {
            position: relative;
            height: 47px;
            width: 47px;
            padding: 0 0 0 8px;
            margin: 0;
            font-family: $additional-text-font;
            font-size: 22px;
            font-weight: 700;
            letter-spacing: .3em;
            background-color: #f5f0e5;
            outline: 0;
        }

        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            width: 55px;
            height: 47px;
            cursor: pointer;
            @include edgtfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }
    }
}

.woocommerce-page .edgtf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */