/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.edgtf-pl-holder {
    @include edgtfRelativeHolderLayout();
	
	&.edgtf-info-on-image {
		
		.edgtf-pli {

			.edgtf-pli-text {
				background-color: rgba(0,0,0,.25);
			}

			.edgtf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .edgtf-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;

		&.edgtf-has-hover-image {

			.edgtf-pli-inner {

				&:hover {
					.edgtf-pli-image .edgtf-attachment-shop-catalog {
						opacity: 1;
					}
				}

				.edgtf-pli-image {
					@include edgtfRelativeHolderLayout();

					.edgtf-attachment-shop-catalog {
						position: absolute;
						top: 0;
						left: 0;
						opacity: 0;
						@include edgtfTransform(translateZ(0));
						@include edgtfTransition(opacity .5s cubic-bezier(.21,.6,.44,2.18));
					}
				}
			}
		}

	    .edgtf-pli-title {
		    margin: 0;
	    }

	    .edgtf-pli-category {
		    margin: 1px 0 0;
		    
		    a {
			    color: $default-text-color;
			    
			    &:hover {
				    color: $first-main-color;
			    }
		    }
	    }

        .edgtf-pli-excerpt {
            margin: 1px 0 0;
        }

	    .edgtf-pli-rating-holder {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 3px 0;
	    }

	    .edgtf-pli-rating {
		    @include edgtfWooCommerceRatingsStyle();
	    }

        .edgtf-pli-price {
	        @include edgtfWooCommercePriceStyle();
        }
	
	    .edgtf-pl-contents {
		    display: block;
		
		    @media only screen and (min-width: $ipad-portrait-plus-pixel) {
			    max-width: 64%;
		    }
	    }
    }

    .edgtf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;
	    
	    @include ipad-landscape {
		    width: 100%;
	    }

        &:hover {

            .edgtf-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .edgtf-pli-image {
            @include edgtfRelativeHolderLayout();
			border: 1px solid $default-border-color;
	        box-sizing: border-box;

            img {
                display: block;
                width: 100%;
            }

            .edgtf-pli-onsale,
            .edgtf-pli-out-of-stock,
            .edgtf-pli-new-product {
	            @include edgtfWooCommerceMarksGeneralStyle();
            }

            .edgtf-pli-onsale {
	            @include edgtfWooCommerceSaleStyle();
            }

            .edgtf-pli-out-of-stock {
	            @include edgtfWooCommerceOutOfStockStyle();
            }

	        .edgtf-pli-new-product {
		        @include edgtfWooCommerceNewProductStyle();
	        }
        }

        .edgtf-pli-text {
            @include edgtfAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            box-sizing: border-box;
            @include edgtfTransition(opacity .2s ease-out);
        }

        .edgtf-pli-text-outer {
	        @include edgtfTableLayout();
	        text-align: center;
        }

        .edgtf-pli-text-inner {
            @include edgtfTableCellLayout();

            .edgtf-pli-title,
            .edgtf-pli-category a,
            .edgtf-pli-excerpt,
            .edgtf-pli-rating,
            .edgtf-pli-price {
                color: #fff;
            }

	        .edgtf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .edgtf-pli-add-to-cart {

		        &.edgtf-default-skin {

			        .button,
			        .added_to_cart {
						color: $default-heading-color;
						background-color: $first-main-color;
						border: 1px solid transparent;

				        &:hover {
							color: #fff;
							background-color: $default-heading-color;
							border: 1px solid transparent;;
				        }
			        }
		        }

		        &.edgtf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }

				span {
					margin: 0 0 0 13px;
				}
	        }

            .button,
            .added_to_cart {
	            @include edgtfButtonDefaultStyle();
	            @include edgtfButtonMediumParams();
				padding: 16px 31px;
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .edgtf-pli-link {
		    @include edgtfAbsoluteHolderLayout();
	    }
    }

    .edgtf-pli-text-wrapper {
        @include edgtfRelativeHolderLayout();
        margin: 21px 0 45px;

		.edgtf-pli-title-wrap {
			@include edgtfTableLayout();
			table-layout: initial;

			.edgtf-pli-title,
			.edgtf-pli-price {
				@include edgtfTableCellLayout(top);
			}
		}

		> p {
			margin: 5px 0;
		}
    }

	&.edgtf-product-info-light {

		.edgtf-pli-inner {

			.edgtf-pli-text-inner {

				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: $header-light-color;
				}

				.edgtf-pli-category a:hover {
					color: $header-light-hover-color;
				}
			}
		}
	}

	&.edgtf-product-info-dark {

		.edgtf-pli-inner {

			.edgtf-pli-text-inner {

				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: $header-dark-color;
				}

				.edgtf-pli-category a:hover {
					color: $header-dark-hover-color;
				}
			}
		}
	}

	/*************** Standard Layout - begin ***************/

	&.edgtf-standard-layout {

		.edgtf-pli {
			float: left;
		}

		&.edgtf-large-space {
			margin: 0 0 -50px;
		}

		&.edgtf-normal-space {
			margin: 0 0 -30px;
		}

		&.edgtf-small-space {
			margin: 0 0 -20px;
		}

		&.edgtf-tiny-space {
			margin: 0 0 -10px;
		}

		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

		@for $i from 0 to length($standard_list_columns) {
			&.edgtf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);

				.edgtf-pli {
					width: $column_width;

					@media only screen and (min-width: $ipad-landscape-plus-pixel) {

						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}

	/*************** Standard Layout - end ***************/

	/*************** Masonry Layout - begin ***************/

	&.edgtf-masonry-layout {

		.edgtf-pl-outer {
			opacity: 0;
		}

		.edgtf-pl-sizer {
			width: 100%;
		}

		.edgtf-pl-gutter {
			width: 0;
		}

		.edgtf-pli-inner {
			width: 100%;
			height: 100%;

			.edgtf-pli-image {
				height: 100%;

				img {
					height: 100%;
				}
			}
		}

		.edgtf-pli.edgtf-woo-image-large-width {

			img {
				width: 100%;
			}
		}

		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');

		@for $i from 0 to length($masonry_list_columns) {
			&.edgtf-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);

				.edgtf-pli,
				.edgtf-pl-sizer {
					width: $column_width;
				}

				.edgtf-pli.edgtf-woo-image-large-width,
				.edgtf-pli.edgtf-woo-image-large-width-height {
					width: $column_width*2;
				}
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */