/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include edgtfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include edgtfRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include edgtfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;

                strong {
                    color: $default-heading-color;
                }
            }
            
            .buttons {
                margin: 10px 0 0;

                a {
                    padding: 12px 22px;
                }
            }
        }
    }
    
    &.widget_layered_nav {
    
        .edgtf-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .edgtf-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {

        .edgtf-widget-title {
            margin: 0 0 28px;
        }

        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 2px;
                background-color: #e5e5e5;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -5px;
                    height: 13px;
                    width: 13px;
                    background-color: $first-main-color;
                    margin: 0 0 0 -1px;
                    border-radius: 100%;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include edgtfTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: relative;
                    top: -1px;
                    height: 4px;
                    display: block;
                    background-color: $default-heading-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 25px 0 0;
            
            .button {
                position: relative;
                float: right;
                padding: 5px 32px;
                color: $default-heading-color;
                background-color: $first-main-color;

                &:after {
                    position: absolute;
                    content: '';
                    top: -6px;
                    left: -6px;
                    width: 100%;
                    height: 100%;
                    padding: 5px;
                    border: 1px solid #000;
                    box-sizing: content-box;
                    @include edgtfTransition(padding .2s cubic-bezier(.21,.6,.44,2.18),top .2s cubic-bezier(.21,.6,.44,2.18),left .2s cubic-bezier(.21,.6,.44,2.18));
                }

                &:hover {
                    color: $default-heading-color;
                    background-color: $first-main-color;

                    &:after {
                        top: -1px;
                        left: -1px;
                        width: 100%;
                        height: 100%;
                        padding: 0;
                        @include edgtfTransition(padding .2s ease,top .2s ease,left .2s ease);
                    }
                }
            }
            
            .price_label {
                float: left;
                line-height: 48px;
                color: $default-heading-color;
            }
        }
    }
    
    &.widget_product_categories {
        margin: 0 0 29px;

	    .edgtf-widget-title {
		    margin: 0 0 18px;
	    }

	    ul {
		    position: relative;

            li {
                margin: 0;
                padding: 4px 0 4px 14px;
				font-size: 15px;
	            line-height: 28px;
                color: $default-heading-color;

	            &:before {
		            content: '';
		            display: block;
		            position: absolute;
		            left: -2px;
		            font-size: 35px;
		            color: $first-main-color;
	            }

                ul {
                    margin: 4px 0 0 10px;
                    padding: 0;

                    li {

                        &:last-child {
                            padding: 4px 0 0 14px;
                        }
                    }
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .edgtf-widget-title {
            margin: 0 0 32px;
        }
        
        ul {
            
            li {
                min-height: 110px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: auto;
                        max-width: 92px;
						max-height: 100%;
                        border: 1px solid $default-border-color;
                        box-sizing: border-box;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 110px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }

					.product-title {
						font-weight: 700;
						text-transform: uppercase;
					}

					.amount {
						color: $first-main-color;
						font-family: $additional-text-font;
						font-size: 15px;
						font-weight: 700;
					}
                }
                
                > *:not(a) {
                    margin: 0 0 0 110px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    margin: 0 0 4px;
                    font-size: 15px;
                    color: inherit;
                }

                .star-rating {
                    margin: 0 0 2px 110px;
                }

                .amount {
                    color: $first-main-color;
					font-family: $additional-text-font;
					font-size: 15px;
					font-weight: 700;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 110px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {

            .edgtf-widget-title {
                margin: 0 0 12px;
            }

            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin: 0 6px 10px 0;
                padding: 0 10px;
                font-family: inherit;
                font-size: 14px !important;
                line-height: 28px;
                color: $default-heading-color;
                background-color: inherit;
                border: 1px solid transparent;
                @include edgtfTransition(color .2s ease-out, background-color .2s ease-out, border-color .2s ease-out);

                &:hover {
                    border-color: $first-main-color;
                }

                text-transform: lowercase;
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include edgtfRelativeHolderLayout();
            clear: both;
            
            > div {
                @include edgtfRelativeHolderLayout();
                border: 1px solid $default-border-color;
                box-sizing: border-box;
            }
    
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                font-family: inherit;
                font-weight: 400;
                font-size: 15px;
                line-height: 24px;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include edgtfTransition(color .2s ease-out);
            }
    
            input {
                width: calc(100% - 44px);
                height: 44px;
                padding: 10px 10px 10px 16px;
                color: #bcbcbc;
        
                &:focus {
                    color: $default-heading-color;
                    background-color: #f5f0e5;
                }
        
                @include edgtfPlaceholder {
                    color: inherit;
                }
            }
    
            button {
                width: 40px;
                height: 40px;
                margin: 2px 2px;
                padding: 0;
                font-size: 16px;
                color: #fff;
                background-color: $first-main-color;
                cursor: pointer;
                @include edgtfTransition(color .2s ease-in-out, background-color .2s ease-in-out);
        
                &:hover {
                    background-color: $default-heading-color;
                }
        
                span {
                    display: block;
                    line-height: inherit;
            
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */