/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin edgtfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 20px;
    width: auto;
    height: 41px;
    margin: 0;
    padding: 0 24px;
    color: $default-heading-color;
    font-size: 12px;
    font-weight: 700;
    line-height: 41px;
    letter-spacing: .12em;
    text-align: center;
    text-transform: uppercase;
    z-index: 100;
    box-sizing: border-box;
    @include edgtfTransform(translateZ(0));
}

@mixin edgtfWooCommerceSaleStyle() {
    left: 20px;
    background-color: #f5f0e5;
}

@mixin edgtfWooCommerceOutOfStockStyle() {
    right: 20px;
    background-color: #f5f0e5;
}

@mixin edgtfWooCommerceNewProductStyle() {
    right: 20px;
    background-color: #f5f0e5;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin edgtfWooCommercePriceStyle($font-size: 25px) {
    position: relative;
    display: block;
    margin: 14px 0 0;
    font-family: $additional-text-font;
    font-size: $font-size;
    line-height: 1.13;
    font-weight: 700;
    color: $first-main-color;

    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: $default-text-color;
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "FontAwesome";
    font-size: 11px;
    line-height: 1;
    letter-spacing: .15px;
    color: $first-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin edgtfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f006\f006\f006\f006\f006";
		letter-spacing: 0.3em;
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f005\f005\f005\f005\f005";
			letter-spacing: 0.3em;
		}
    }
}

@mixin edgtfWooCommerceSingleProductRatingsStyle() {

    &:before {
        content: "\f006";
        position: relative;
        display: inline-block;
        vertical-align: middle;
		letter-spacing: 0.3em;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f005\f005\f005\f005\f005";
        position: absolute;
        right: 0;
        bottom: 0;
        display: block;
		letter-spacing: 0.3em;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */