/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.edgtf-plc-holder {
	@include edgtfRelativeHolderLayout();
	
	.edgtf-owl-slider .owl-nav {
		
		@include phone-landscape {
			display: none;
		}
		
		.owl-prev {
			left: -50px;
		}
		
		.owl-next {
			right: -50px;
		}
	}

	&.edgtf-info-on-image {

		.edgtf-plc {

			.edgtf-plc-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

	.edgtf-plc {
		position: relative;
		display: inline-block;
		vertical-align: top;
		width: 100%;
		padding: 0;
		z-index: 100;
		box-sizing: border-box;

		.edgtf-plc-title {
			margin: 0 0 4px;
		}

		.edgtf-plc-category {
			margin: 11px 0 3px;
		}

		.edgtf-plc-excerpt {
			color: $default-heading-color;
			line-height: 1.3em;
			margin: 11px 0 3px;
		}

		.edgtf-plc-rating-holder {
			@include edgtfRelativeHolderLayout();
			line-height: 1;
			margin: 10px 0 3px;
		}

		.edgtf-plc-rating {
			@include edgtfWooCommerceRatingsStyle();
		}

		.edgtf-plc-price {
			@include edgtfWooCommercePriceStyle();
		}

		.edgtf-pl-contents {
			display: block;

			@media only screen and (min-width: $ipad-portrait-plus-pixel) {
				max-width: 64%;
			}
		}

		&.edgtf-has-hover-image {

			.edgtf-plc-inner {

				&:hover {
					.edgtf-plc-image .edgtf-attachment-shop-catalog {
						opacity: 1;
					}
				}

				.edgtf-plc-image {
					@include edgtfRelativeHolderLayout();

					.edgtf-attachment-shop-catalog {
						position: absolute;
						top: 0;
						left: 0;
						opacity: 0;
						@include edgtfTransform(translateZ(0));
						@include edgtfTransition(opacity .5s cubic-bezier(.21,.6,.44,2.18));
					}
				}
			}
		}
	}

	.edgtf-plc-inner {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		border: 1px solid $default-border-color;
		overflow: hidden;
		box-sizing: border-box;
		width: calc(100% - 1px);

		&:hover {

			.edgtf-plc-text {
				opacity: 1;
			}
		}

		.touch & {
			cursor: pointer;
		}

		.edgtf-plc-image {
			@include edgtfRelativeHolderLayout();

			img {
				display: block;
				width: 100%;
			}

			.edgtf-plc-onsale,
			.edgtf-plc-out-of-stock,
			.edgtf-plc-new-product {
				@include edgtfWooCommerceMarksGeneralStyle();
			}

			.edgtf-plc-onsale {
				@include edgtfWooCommerceSaleStyle();
			}

			.edgtf-plc-out-of-stock {
				@include edgtfWooCommerceOutOfStockStyle();
			}

			.edgtf-plc-new-product {
				@include edgtfWooCommerceNewProductStyle();
			}
		}

		.edgtf-plc-text {
			@include edgtfAbsoluteHolderLayout();
			padding: 10px;
			opacity: 0;
			box-sizing: border-box;
			@include edgtfTransition(opacity .2s ease-out);
		}

		.edgtf-plc-text-outer {
			@include edgtfTableLayout();
			text-align: center;
		}

		.edgtf-plc-text-inner {
			@include edgtfTableCellLayout();

			.edgtf-plc-title,
			.edgtf-plc-category a,
			.edgtf-plc-excerpt,
			.edgtf-plc-rating,
			.edgtf-plc-price {
				color: #fff;
			}

			.edgtf-plc-category a:hover {
				color: rgba(#fff, .8);
			}
		}

		.edgtf-plc-link {
			@include edgtfAbsoluteHolderLayout();
		}
	}
	
	&.edgtf-simple-layout {
		
		.edgtf-plc-add-to-cart {
			position: absolute;
			top: 50%;
			@include edgtfTransform(translateY(-50%));
			@include edgtfTransition(transform 0.34s);
		}
	}

	.edgtf-plc-add-to-cart {

		&.edgtf-default-skin {

			.button,
			.added_to_cart {
				@include edgtfButtonSolidColor();

				&:hover {
					@include edgtfButtonSolidHoverColor();
				}
			}
		}

		&.edgtf-light-skin {

			.button,
			.added_to_cart {
				color: $default-heading-color;
				background-color: #fff;

				&:hover {
					color: #fff;
					background-color: $first-main-color;
				}
			}
		}

		span {
			margin: 0 0 0 13px;
		}
	}

	.button,
	.added_to_cart {
		@include edgtfButtonDefaultStyle();
		@include edgtfButtonMediumParams();
		padding: 16px 30px;
		white-space: nowrap;
		z-index: 3;
	}

	.button.added {
		display: none;
	}

	.edgtf-plc-text-wrapper {
		@include edgtfRelativeHolderLayout();
		margin: 22px 0 0;
		text-align: left;

		.edgtf-plc-title-wrap {
			@include edgtfTableLayout();
			table-layout: initial;

			.edgtf-plc-title,
			.edgtf-plc-price {
				@include edgtfTableCellLayout(top);
			}
		}

		> p {
			margin: 5px 0;
		}
	}

	&.edgtf-product-info-light {

		.edgtf-plc-inner {

			.edgtf-plc-text-inner {

				.edgtf-plc-title,
				.edgtf-plc-category a,
				.edgtf-plc-excerpt,
				.edgtf-plc-rating,
				.edgtf-plc-price {
					color: #fff;
				}

				.edgtf-plc-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}

	&.edgtf-product-info-dark {

		.edgtf-plc-inner {

			.edgtf-plc-text-inner {

				.edgtf-plc-title,
				.edgtf-plc-category a,
				.edgtf-plc-excerpt,
				.edgtf-plc-rating,
				.edgtf-plc-price {
					color: $default-heading-color;
				}

				.edgtf-plc-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}

	/*************** Standard Layout - begin ***************/

	&.edgtf-standard-layout {

		.edgtf-plc {
			float: left;
		}

		&.edgtf-large-space {
			margin: 0 0 -50px;
		}

		&.edgtf-normal-space {
			margin: 0 0 -30px;
		}

		&.edgtf-small-space {
			margin: 0 0 -20px;
		}

		&.edgtf-tiny-space {
			margin: 0 0 -10px;
		}

		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

		@for $i from 0 to length($standard_list_columns) {
			&.edgtf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);

				.edgtf-plc {
					width: $column_width;

					@media only screen and (min-width: $ipad-landscape-plus-pixel) {

						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}

	/*************** Standard Layout - end ***************/

	/*************** Masonry Layout - begin ***************/

	&.edgtf-masonry-layout {

		.edgtf-pl-outer {
			opacity: 0;
		}

		.edgtf-pl-sizer {
			width: 100%;
		}

		.edgtf-pl-gutter {
			width: 0;
		}

		.edgtf-plc-inner {
			width: 100%;
			height: 100%;

			.edgtf-plc-image {
				height: 100%;

				img {
					height: 100%;
				}
			}
		}

		.edgtf-plc.edgtf-woo-image-large-width {

			img {
				width: 100%;
			}
		}

		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');

		@for $i from 0 to length($masonry_list_columns) {
			&.edgtf-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);

				.edgtf-plc,
				.edgtf-pl-sizer {
					width: $column_width;
				}

				.edgtf-plc.edgtf-woo-image-large-width,
				.edgtf-plc.edgtf-woo-image-large-width-height {
					width: $column_width*2;
				}
			}
		}
	}

	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */