/* Woocommerce Product Page shortcode style - begin
========================================================================== */

div.woocommerce {

    > .single-product {
	    @include edgtfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }

	.edgtf-single-product-content {
		@include edgtfRelativeHolderLayout();
		clear: both;
		margin: 0 0 90px;

		.images {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			width: 51%;
			margin: 0;
			padding: 0 25px 0 0;
			float: left;
			box-sizing: border-box;

			figure {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0 -10px;

				a {
					@include edgtfRelativeHolderLayout();

					img {
						position: relative;
						width: 100%;
						display: block;
						border: 1px solid #c8c8c8;
					}
				}
			}

			.woocommerce-product-gallery__trigger {
				position: absolute;
				top: 10px;
				right: 39px;
				width: 30px;
				height: 30px;
				line-height: 30px;
				text-align: center;
				z-index: 10;
			}

			.woocommerce-product-gallery__image {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0 10px;
				box-sizing: border-box;

				&:first-child {
					left: 10px;
					width: calc(100% - 20px);
					padding: 0;
				}

				.zoomImg {
					cursor: crosshair;
				}
			}

			&.woocommerce-product-gallery--with-images {

				.woocommerce-product-gallery__image {

					&:not(:first-child) {
						float: left;
						padding-top: 20px;
					}
				}
			}

			$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
			@for $i from 0 to length($thumb_3_columns_label) {
				&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
					$column_width: 100%/($i+2);

					.woocommerce-product-gallery__image {

						&:not(:first-child) {
							width: $column_width;
						}
					}
				}
			}
		}
	}

	.edgtf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 49%;
        padding: 0 0 0 13px;
        float: left;
        box-sizing: border-box;

	    .edgtf-single-product-title {
		    display: block;
		    margin: 0 0 5px;
	    }

	    .woocommerce-product-rating {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 12px 0 6px;
	    }

	    .price {
		    @include edgtfWooCommercePriceStyle(36px);
	    }

	    .woocommerce-product-details__short-description {
		    @include edgtfRelativeHolderLayout();
		    margin: 68px 0 0;

		    p {
			    margin: 0;
		    }
	    }

	    .product_meta {
		    margin: 104px 0 0;
		    padding: 30px 0 22px;
		    border-top: 1px solid $default-border-color;
		    border-bottom: 1px solid $default-border-color;

		    > span {
			    display: table;
			    width: 100%;
				padding: 3px 0;

			    > * {
				    display: table-cell;
				    vertical-align: top;
			    }

			    .edgtf-woo-meta-title {
				    width: 107px;
				    font-weight: 700;
				    text-transform: uppercase;
				    color: $default-heading-color;
			    }

			    a {
				    color: $default-text-color;

				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }

	    .edgtf-woo-social-share-holder {
		    @include edgtfRelativeHolderLayout();
		    margin: 23px 0 0;

		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }

		    > span {
			    padding: 0 47px 0 0;
			    font-weight: 700;
			    text-transform: uppercase;
			    color: $default-heading-color;
		    }

		    .edgtf-social-share-holder {

			    a {
				    color: $first-main-color;

				    &:hover {
					    color: $default-heading-color;
				    }
			    }
		    }
	    }

	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }

	    form.cart {
		    @include edgtfRelativeHolderLayout();
		    margin: 84px 0 0;
	    }

	    .edgtf-quantity-buttons {
		    margin: 0 20px 0 0;
	    }
    }

    .woocommerce-tabs {
        @include edgtfRelativeHolderLayout();
		margin: 0;
		padding: 54px 0 52px;
		text-align: center;
		border-top: 1px solid $default-border-color;
		border-bottom: 1px solid $default-border-color;

        ul.tabs {
            position: relative;
		    display: inline-block;
		    vertical-align: middle;
			margin: 0;
			padding: 0;
			list-style: none;
			border-bottom: 1px solid $default-border-color;

            > li {
                position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;
				margin: 0 0 -2px;
				padding: 0;

				&.active a {
					border-color: $first-main-color;
				}

				a {
					position: relative;
					display: inline-block;
					vertical-align: middle;
					padding: 11px 20px;
					font-size: 19px;
					line-height: 28px;
					font-weight: 700;
					text-transform: uppercase;
					color: $default-heading-color;
					border-bottom: 3px solid transparent;
					box-sizing: border-box;
					@include edgtfTransition(color .2s ease-out, background-color .2s ease-out, border-color .2s ease-out);

					&:hover {
						border-color: $first-main-color;
					}
				}
            }
        }

        .entry-content {
            @include edgtfRelativeHolderLayout();
			padding: 32px 0;
			box-sizing: border-box;

			> h2 {
				display: none;
			}

			> p {
				margin: 0;
			}
        }

        table {
			text-align: left;

			tr, th {
				border: 0;
			}

			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;

				p {
					margin: 0;
				}
			}

			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
			}

			td {
				width: 99%;
			}
		}

        #reviews {
            @include edgtfRelativeHolderLayout();
			text-align: left;

			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}

            ol.commentlist {
				list-style: none;

				li {
					margin: 0 0 15px;

					&:last-child {
						margin: 0;
					}
				}

				.comment_container {
					@include edgtfRelativeHolderLayout();

					img {
						float: left;
						width: 70px;
					}
				}

				.comment-text {
					padding: 0 0 0 90px;

					p {
						margin: 0;
					}

					.meta {

						.woocommerce-review__author {
							color: $default-heading-color;
						}
					}
				}
			}

            .comment-respond {
				margin-top: 15px;

				.comment-reply-title {
					display: inline-block;
					margin: 8px 0 24px;
					font-size: 18px;
					line-height: 24px;
					font-weight: 700;
					color: $default-heading-color;
				}

				p {
					margin: 0;
				}

				.comment-form-comment {
                    margin: 3px 0 0;
                }

				.stars {
					overflow: hidden;

					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include edgtfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
        }
    }

    .related.products,
	.upsells.products {
		margin: 84px 0 0;

		> h2 {
			display: block;
			margin: 0 0 39px;
			font-size: 27px;
			text-transform: uppercase;
		}
	}
}

/* Woocommerce Product Page shortcode style - end
========================================================================== */