/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.edgtf-woo-single-page {

	&.edgtf-woo-single-thumb-on-left-side {

		.edgtf-single-product-content {

			.images {

				@media only screen and (min-width: $ipad-portrait-plus-pixel) {

					figure {
						width: 100%;
						margin: 0;
					}

					.woocommerce-product-gallery__image {

						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}

					&.woocommerce-product-gallery--with-images {

						.woocommerce-product-gallery__image {

							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;

								a {
									height: 100%;

									img {
										height: 100%;
									}
								}
							}

							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);

								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});

									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}

    .product {
	    @include edgtfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }

    .edgtf-single-product-content {
        @include edgtfRelativeHolderLayout();
        clear: both;
        margin: 0 0 90px;

	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0 16px 0 0;
		    float: left;
		    box-sizing: border-box;

		    figure {
			    position: relative;
			    display: block;
			    margin: 0 -10px;

			    a {
				    @include edgtfRelativeHolderLayout();

				    img {
					    position: relative;
					    width: 100%;
					    display: block;
					    border: 1px solid #e8e8e8;
					    box-sizing: border-box;
				    }
			    }
		    }

		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }

		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;

			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }

			    .zoomImg {
				    cursor: crosshair;
			    }
		    }

		    &.woocommerce-product-gallery--with-images {

			    .woocommerce-product-gallery__image {

				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }

		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);

				    .woocommerce-product-gallery__image {

					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
	.star-rating {
		font-size: 15px;
	}

    .edgtf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 0 0 0 24px;
        float: left;
        box-sizing: border-box;

	    .edgtf-single-product-title {
		    display: block;
		    margin: 0 0 5px;
	    }

	    .woocommerce-product-rating {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 12px 0 6px;
	    }

	    .price {
		    @include edgtfWooCommercePriceStyle(36px);
	    }

	    .woocommerce-product-details__short-description {
		    @include edgtfRelativeHolderLayout();
		    margin: 68px 0 0;

		    p {
			    margin: 0;
		    }
	    }

	    .product_meta {
		    margin: 104px 0 0;
		    padding: 30px 0 22px;
		    border-top: 1px solid $default-border-color;
		    border-bottom: 1px solid $default-border-color;

		    > span {
			    display: table;
			    width: 100%;
				padding: 3px 0;

			    > * {
				    display: table-cell;
				    vertical-align: top;
			    }

			    .edgtf-woo-meta-title {
				    width: 130px;
				    font-weight: 700;
				    text-transform: uppercase;
				    color: $default-heading-color;
			    }

			    a {
				    color: $default-text-color;

				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }

	    .edgtf-woo-social-share-holder {
		    @include edgtfRelativeHolderLayout();
		    margin: 23px 0 0;

		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }

		    > span {
				padding: 0 70px 0 0;
			    font-weight: 700;
			    text-transform: uppercase;
			    color: $default-heading-color;
		    }

		    .edgtf-social-share-holder {

			    a {
				    color: $first-main-color;

				    &:hover {
					    color: $default-heading-color;
				    }
			    }
		    }
	    }

	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
			text-transform: uppercase;
	    }

	    form.cart {
		    @include edgtfRelativeHolderLayout();
		    margin: 84px 0 0;
	    }

	    .edgtf-quantity-buttons {
		    margin: 0 20px 0 0;
	    }
    }

	.woocommerce-tabs {
		@include edgtfRelativeHolderLayout();
		margin: 0;
		padding: 54px 0 52px;
		text-align: center;
		border-top: 1px solid $default-border-color;
		border-bottom: 1px solid $default-border-color;

		ul.tabs {
			position: relative;
		    display: inline-block;
		    vertical-align: middle;
			margin: 0;
			padding: 0;
			list-style: none;
			border-bottom: 1px solid $default-border-color;

			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;
				margin: 0 0 -2px;
				padding: 0;

				&.active a {
					border-color: $first-main-color;
				}

				a {
					position: relative;
					display: inline-block;
					vertical-align: middle;
					padding: 11px 20px;
					font-size: 19px;
					line-height: 28px;
					font-weight: 700;
					text-transform: uppercase;
					color: $default-heading-color;
					border-bottom: 3px solid transparent;
					box-sizing: border-box;
					@include edgtfTransition(color .2s ease-out, background-color .2s ease-out, border-color .2s ease-out);

					&:hover {
						border-color: $first-main-color;
					}
				}
			}
		}

		.entry-content {
			@include edgtfRelativeHolderLayout();
			padding: 32px 0;
			box-sizing: border-box;

			> h2 {
				display: none;
			}

			> p {
				margin: 0;
			}
		}

		table {
			text-align: left;
			
			&.shop_attributes {
				margin-top: 9px;
			}

			tr, th {
				border: 0;
			}

			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;

				p {
					margin: 0;
				}
			}

			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				text-transform: uppercase;
			}

			td {
				width: 99%;
			}
		}

		#reviews {
			@include edgtfRelativeHolderLayout();
			text-align: left;

			h2 {
				position: relative;
				font-size: 27px;
				line-height: 1.3em;
				margin: 13px 0 80px;
				
				&:after {
					content: '';
					position: relative;
					display: block;
					margin-top: 5px;
					width: 62px;
					height: 9px;
					background: url(../img/beer-stroke.svg) 0 no-repeat;
					background-size: 62px 9px;
				}
			}

			ol.commentlist {
				list-style: none;

				li {
					padding: 0 0 47px;
					margin: 0 0 56px;
					border-bottom: 1px solid $default-border-color;

					&:last-child {
						padding: 0;
						border-bottom: 0;
					}
				}

				.comment_container {
					@include edgtfRelativeHolderLayout();

					img {
						margin-bottom: 10px;
						width: 80px;
						border-radius: 100%;
						
						@media only screen and (min-width: $ipad-portrait-plus-pixel) {
							float: left;
							margin-bottom: 0;
						}
					}
				}

				.comment-text {
					padding: 0;
					
					@media only screen and (min-width: $ipad-portrait-plus-pixel) {
						padding: 0 100px 0 110px;
					}
					
					p {
						margin: 0;
					}
					
					.star-rating {
						
						@media only screen and (min-width: $ipad-portrait-plus-pixel) {
							position: absolute;
							top: 0;
							right: 0;
						}
					}
					
					.woocommerce-review__author {
						display: block;
						font-size: 19px;
						font-weight: 700;
						text-transform: uppercase;
						color: $default-heading-color;
					}
					
					.woocommerce-review__dash {
						display: none;
					}
					
					.woocommerce-review__published-date {
						display: block;
						margin: 5px 0 0;
						font-size: 12px;
						line-height: 1;
						color: $first-main-color;
					}
					
					.description {
						margin: 28px 0 0;
					}
				}
			}

			#review_form_wrapper {
				@include edgtfRelativeHolderLayout();
				margin-top: 8px;
			}
			
			.comment-respond {

				.comment-reply-title {
					display: block;
					margin: 8px 0 60px;
					font-size: 27px;
					line-height: 1.3em;
					font-weight: 700;
					text-transform: uppercase;
					color: $default-heading-color;
				}
				
				form {
					position: relative;
				}

				p {
					margin: 15px 0 0;
					
					&.comment-notes {
						position: relative;
						display: block;
						margin: 0 0 21px;
						
						@media only screen and (min-width: $ipad-portrait-plus-pixel) {
							padding-right: 200px;
						}
					}
				}

				.comment-form-comment {
                    margin: 3px 0 0;
                }
				
				.comment-form-rating {
					
					@media only screen and (min-width: $ipad-portrait-plus-pixel) {
						position: absolute;
						top: 0;
						right: 0;
						
						> * {
							position: relative;
							display: inline-block;
							vertical-align: top;
						}
						
						label {
							line-height: inherit;
							margin-right: 15px;
							color: $default-heading-color;
						}
						
						.stars {
							display: inline-block;
							vertical-align: top;
							margin: 0;
							line-height: 1;
						}
					}
				}

				.stars {
					overflow: hidden;

					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include edgtfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}

	.related.products,
	.upsells.products {
		margin: 84px 0 0;

		> h2 {
			display: block;
			margin: 0 0 39px;
			font-size: 27px;
			text-transform: uppercase;
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */